import React from "react";

import Page from '../components/layout/page';
import PageHead from "../components/base/head-base";
import { LayoutContainer } from '../components/layout/layout';
import BlogContent from "../components/page-content/blog/blog-content";

const Blog = () => {
    return (
        <>
            <Page active="blog">
                <LayoutContainer>
                    <BlogContent />
                </LayoutContainer>
            </Page>
        </>
    )
};

export const Head = ({ location }) => {
    return (
        <PageHead
            pathname={location.pathname}
            title="Blog - Jan Wagenaar"
        />
    )
};

export default Blog;