import React from "react";
import { GatsbyImage } from "gatsby-plugin-image";
import styled from "styled-components";

import breakpoints from "../layout/breakpoints";
import { Heading, Text } from "../base/typography";
import BaseLink from "../../components/base/links/base-link";
import TextLink from "../../components/base/links/text-link";

const BlogItem = ({ item }) => {

    return (
        <StyledBlogItem>
            <BaseLink
                to={`/blog/${item.slug}`}
            >
                <StyledBlogCover>
                    <GatsbyImage
                        image={item.image}
                        alt=""
                        layout="fullWidth"
                        objectFit="cover"
                        objectPosition="50% 50%"
                    />
                </StyledBlogCover>
            </BaseLink>
            <div>
                <BaseLink
                    to={`/blog/${item.slug}`}
                >
                    <Heading
                        level={4}
                    >
                        {item.title}
                    </Heading>
                </BaseLink>
                <Text
                    noTopMargin
                >
                    {item.content}
                </Text>
                <TextLink
                    to={`/blog/${item.slug}`}
                >
                    Read more
                </TextLink>
            </div>
        </StyledBlogItem>
    )
};

const StyledBlogItem = styled.section`
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 40rem;
    word-break: break-word;

    ${breakpoints.md`
        & {
            flex-direction: row;
            gap: var(--spacing-m);
        }
    `}
`;

const StyledBlogCover = styled.div`
    display: flex;
    justify-content: center; 
    align-items: center;
    height: 10rem;
    width: 100%;
    overflow: hidden;
    margin-bottom: var(--spacing-m);

    & .gatsby-image-wrapper {
        height: 100%;
    }

    ${breakpoints.md`
        & {
            margin-bottom: 0;
            width: 10rem;
        }
    `}
`

export default BlogItem;